.slide {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center;
  transition: transform 0.5s ease;
}

.slide.active {
  transform: translateX(-100%);
}

.slick-dots li.slick-active button::before {
  opacity: 0.75;
  color: #2f4efb;
}

.slick-dots li button::before {
  font-family: inherit;
}
