* {
  margin: 0px;
  padding: 0px;
}
#footercont {
  background-color: black;
  color: white;
  line-height: 40px;
  padding-bottom: 20px;
}
#footerchild {
  width: 80%;
  margin: auto;
}
#footerinsurance {
  display: flex;
  justify-content: space-evenly;
}
.icontextfooter {
  display: flex;
  flex-direction: row;
  gap: 15px;
  align-items: center;
}
#tc {
  font-size: 12px;
  line-height: 22px;
}
#footermidpart {
  background-color: #202020;
  color: white;
  line-height: 40px;
  padding: 20px 0px;
}
#footermidchild {
  width: 80%;
  margin: auto;
  display: flex;
  justify-content: space-evenly;
  padding-bottom: 20px;
  list-style: none;
  color: #a5a5a5;
  font-weight: 700;
}
#footermidchildnext {
  width: 80%;
  margin: auto;
  display: flex;
  justify-content: right;
  gap: 20px;
}
#footerchildend {
  display: flex;
  gap: 10px;
}
.footerendicons img {
  height: 30px;
  padding: 2px;
  background-color: white;
  border-radius: 5px;
}
#footerendparent {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

@media screen and (max-width: 1024px) {
  #footerinsurance {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    justify-content: space-evenly;
  }
  #footermidchild {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    line-height: 20px;
    text-align: center;
  }
  #footermidchildnext {
    justify-content: center;
  }
  #footerchildend {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media screen and (max-width: 450px) {
  #footerinsurance {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    justify-content: space-evenly;
  }
  #footermidchild {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    line-height: 20px;
    text-align: center;
  }
  #footermidchildnext {
    justify-content: center;
  }
  #footerchildend {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
  }
}

.foottt {
  display: flex;
  flex-direction: column;
}

.footer-icon {
  background: url(../Resources/img32.svg) no-repeat;
  width: 35px;
  height: 40px;
  background-size: 100%;
  position: absolute;
  top: -10px;
  left: 0;
}

.footer-icon.life-insurance {
  background-position: 0 -76px;
  position: initial;
  margin: 5px 2px 0 10px;
}

.footer-icon.other-insurance {
  background-position: 0 -299px;
  left: -50px;
  position: initial;
  margin: 5px 2px 0 10px;
}

.footer-icon.health-insurance {
  background-position: 0 -150px;
  left: 0;
  position: initial;
  margin: 5px 2px 0 10px;
}

.footer-icon.investment {
  background-position: 0 -225px;
  left: 0;
  position: initial;
  margin: 5px 2px 0 10px;
}

.footer-icon.general-insurance {
  background-position: 0 0;
  position: initial;
  margin: 5px 2px 0 10px;
}

.foote {
  display: flex;
  flex-direction: row;
}
